import { Controller } from '@ali/4ever-cangjie';
import { isKeyHotkey } from 'is-hotkey';
import { clearStyles } from '../actions';

const isClearStyleHotkey = isKeyHotkey('mod+\\');

export default function onKeyDown(event: React.KeyboardEvent, controller: Controller, next: () => void) {
  if (isClearStyleHotkey(event)) {
    event.preventDefault();
    return controller.run('onAction', clearStyles());
  }
  return next();
}
